<template>
  <div class="login_page">
      <div class="login_img_bg">
        <img src="../assets/login.png" alt="">
      </div>
        <div class="login_form">
          <div class="login_forms">
              <div class="logo_sport">
                <img src="../assets/logo.svg" alt="">
              </div>
            <div>
              <div class="text_login">Hakamlar tizimiga kirish</div>
              <form @submit.prevent="postLogin()">
                  <div>
                    <input type="text" placeholder="Username" v-model="username">
                  </div>
                <div>
                  <input type="text" placeholder="Parol" v-model="password">
                </div>
                <div class="login_btn">
                    <button>
                      <span v-if="!spin">Kirish</span>
                      <b-spinner variant="white" v-if="spin" ></b-spinner>
                    </button>
                </div>
              </form>
            </div>
          </div>
        </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      username:'',
      password:'',
      isLoading:false,
      spin:false
    }
  },
  methods: {
    postLogin() {
      this.spin=true
      this.isLoading = true


      this.base_url.post('/login/', {
        "username": this.username,
        "password": this.password
      })
          .then(res => {
            localStorage.setItem("token", res.data.access)
            this.$router.push('/')
          })
          .catch(() => {
            this.isLoading = false
            this.spin=false
            this.$toast("Login yoki parol xato", {
              timeout: 2000,
              type:"error"
            });
          })

    }
  }
}
</script>
